import React from 'react'
import CartItem from './CartItem'

export default function CartList({value}) {
    // codeGame shortcu
   
    const {cart}=value
    return (
        <div className="container-fluid ">
            {cart.map(item=>{
                return <CartItem  key={item.id}
                item={item} value={value} />
                // codeGame

            })}
        </div>
    )
}
