import React, { Component } from 'react'
import Hero2 from '../components/pliki zamienne/Hero2'
import Banner2 from '../components/pliki zamienne/Banner2'
import ProductList from '../components/ProductList'

import { Link } from 'react-router-dom'

export default class Home extends Component {
    render() {
        return (
            <React.Fragment>
              
                <Hero2 hero="defaultHero">
                    <Banner2
                        title="Domowy Sernik"
                        subtitle="jolanta pluskota"
                    >
                        <Link to='/produkty'>
                            <button className="btn-main">sprawdź tutaj</button>
                        </Link>
                    </Banner2>
                </Hero2>
                <ProductList />
            </React.Fragment>
        )
    }
}
