import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import logo from '../logo.svg'
import styled from 'styled-components'
import { ButtonContainer } from './Button'
import { FaAlignRight } from 'react-icons/fa'


export default class Navbar extends Component {
    state = {
        isOpen: false
    }

    handleToogle = () => {
        this.setState({ isOpen: !this.state.isOpen })
    }

    render() {
        return (
            <React.Fragment>

                <NavWrapper className="navbar navbar-expand-sm navbar-dark px-sm-5">
                    {/* 
            /*
https://www.iconfinder.com/icons/1243689/call_phone_icon
Creative Commons (Attribution 3.0 Unported);
https://www.iconfinder.com/Makoto_msk */ }
                    <Link to="/">
                        <img src={logo} alt="nasze logo" className="navbar-brand" />
                    </Link>


                    <ul className="navbar-nav align-items-center">
                        <li className="nav-item ml-5">
                            <Link to="/" className="nav-link">Produkty</Link>
                        </li>
                        <ul className={this.state.isOpen ? "nav-links nav-item ml-5 hide align-items-center" : "nav-links"} >
                            <li><Link to="/przepisy" className="nav-link"> Przepisy</Link></li>
                            <li className={this.state.isOpen ? "nav-links show-nav " : "nav-links"} >
                                <Link to="/aboutMe" className="nav-link">O mnie</Link>
                            </li>
                        </ul>


                    </ul>


                    <Link to='/cart' className="ml-auto">
                        <ButtonContainer>
                            <span className="mr-2">
                                <i className="fas fa-cart-plus">Koszyk</i>
                            </span>
                        </ButtonContainer>
                    </Link>
                    {/* hamburger_____________ */}
                    <button
                        type="button"
                        className="nav-btn"
                        onClick={this.handleToogle}
                    >
                        <FaAlignRight className='nav-icon' />
                    </button>

                    {/* hamburger end ____________ */}
                  

                </NavWrapper>
                <ul className={this.state.isOpen ? "nav-links show-nav " : "nav-links hide "}>
                    <li><Link to="/"> Jeden</Link></li>
                    <li><Link to="/rooms">rooms</Link></li>

                </ul>



            </React.Fragment >
        )
    }
}

const NavWrapper = styled.nav
    `
    height:60px;
background:var(--mainBlue);
.nav-link{
    color:var(--mainWhite)!important
    font-size:0.3rem;
    text-transform:uppercase !important;

}

`

