import React from 'react'

export default function CartItem({ item, value }) {
    const { id, title, img, price, total, count } = item;
    const { increment, decrement, removeItem } = value
    return (
        <div className="row my-2 text-capitalize text-center myItemRow">
            <div className="col-10 mx-auto col-lg-2">
                <img src={img}
                    style={{ width: '5rem', height: "5rem" }}
                    // codeGame
                    className="img-fluid" alt="product"
                />
            </div>
            <div className="col-10 mx-auto col-lg-2">
                <span className="d-lg-none">Produkt: </span>
                {title}
            </div>
            <div className="col-10 mx-auto col-lg-2">
                <span className="d-lg-none">Cena: </span>
                {price}
            </div>
            <div className="col-10 mx-auto col-lg-2 my-2 my-lg-0">
                {/* buttons */}
                <div className="d-flex justify-content-center">
                    <div>
                        <span className="btn btn-black mx-1" onClick={() => decrement(id)}>- </span>
                        <span className="btn btn-black mx-1">{count}</span>
                        <span className="btn 
                        btn-black mx-1" onClick={() => increment(id)}>+ </span>

                    </div>
                </div>
                {/* buttons */}
            </div>
            {/*  */}
            <div className="col-10 mx-auto col-lg-2">
                <div className="cart-icon" onClick={()=>removeItem(id)}>
                    <i className="fas fa-trash"></i>

                    </div>
            </div>
            <div className="col-10 mx-auto col-lg-2">
                <strong>iSuma:  {total} złrrrrr</strong>

            </div>

        </div>
    )
}
